.projects {
  margin-top: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 85vh;
}
.projects > span {
  color: var(--orange);
  font-size: 2.5rem;
  font-weight: bold;
}
.projects .swiper {
  overflow: visible !important;
}
.projects-slider {
  margin-top: 3rem;
  width: 100%;
}
.projects-slider .swiper-slide {
  width: 35rem;
}
.projects img {
  width: 34rem;
  filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
  border-radius: 19px;
}
